import Link from 'next/link';
import injectSheet from 'react-jss'
import Button from './Button.js'

const styles =  {    
  logo: {
    width: '100%',
    textAlign: 'center',
    marginTop: '1rem',
    '& img':{
      position: 'relative',
      width: '100%'
    }
  } 
}

const Header = ({classes}) => (
<div>
  <div className={classes.logo}>
    <Link href="/">
      <a><img src="/img/logo.png"/></a>
    </Link>
  </div>
  <Button></Button>
  <Link href="/" >
    <a>Next</a>
  </Link>
  <Link href="/" >
    <a>Previous</a>
  </Link>
</div>
);

export default injectSheet(styles)(Header);